import app from './_firebase';

import axios from 'axios';
import XLSX from 'xlsx' ;
import { API_URL } from '../config';

export const addSchool = async (data) => {
    try {
        var docRef = await app.firestore().collection("schools").doc(data.id);
        data.total_student=0;
        data.total_teacher=0;
        data.lms_url=data.id.trim()+".dschool.vn";
        const doc = docRef.get()
        if (doc.exists) {
            console.log("doc exist")
            return "existed";
        } else {
            console.log(data);
            let school  = await app.firestore().collection('schools').doc(data.id).set(data);
            console.log("add school success");
        }
        
    } catch (err) {
        console.log(err);
    }
};

export const getSchoolApi = async (id) => {
    try {
        let querySnapshot  = await app.firestore().collection('schools').doc(id).get();
        const school = querySnapshot.data()
        school.id=id
          return school
    } catch (err) {
        console.log(err);
    }
}

export const updateSchool = async (data) => {
    try {
        let id=data.id;
        let result  = await app.firestore().collection('schools').doc(id).update(data);
        return result
    } catch (err) {
        console.log(err);
    }
};

export const listAllSchool = async (data) => {
    try {
        let querySnapshot  = await app.firestore().collection('schools').get();
        const school = querySnapshot.docs.map((doc) => {
            return { id: doc.id, ...doc.data() }
          })
          return school
    } catch (err) {
        console.log(err);
    }
}

export const has_running_command = async (school_id) => {
    try {
       
        let querySnapshot = await app.firestore().collection('commands').where("data.school_id","==",school_id).where("type","==","school").where("action","==","upload").where("status","==",1).get();
        const commands = querySnapshot.docs.map((doc) => {
            return {
              id: doc.id,
              ...doc.data()
            };
          });
          console.log(commands);
          return commands;
        
      } catch (err) {
        console.log(err);
      }
}

export const deleteSchools = (ids) => {
    try {
      ids.map((id) => {
        app.firestore().collection('schools').doc(id).delete();
      })
    } catch (err) {
      console.log(err);
    }
  };

